  async onUpButton() {
    //
    if (!this.onUpButton_display) { return } else {
      var _this = this;


      if (this.phonenumber.trim().length != 11) {
        //
        let toast = await this.toastCtrl.create({
          message: '',
          duration: 2000,
          position: 'top',
          cssClass:"md-toast",
        });
        toast.present();
        return;
      }

      //--
      let toast = await this.toastCtrl.create({
        message: '',
        duration: 1500,
        position: 'top',
        cssClass:"md-toast",
      });
      toast.present();
      let obj = {
        type: "sendVerificationCode",
        phonenumber: this.phonenumber
      }
      this.ishowVCodeIput = true;//

      //this.send(JSON.stringify(obj), false);


      this.appMin.get(obj).subscribe((data) => {
        _this.dataHandle(data);
      })


      myLocalStorage.setItem('userPhonenumber', this.phonenumber, 30);

      //--

      this.SendButton = true;
      this.onUpButton_display = false;
      //
      var setTime = setInterval(function () {
        WitePrompt("_this.Time" + _this.Time);
        if (_this.Time == 0) {
          clearInterval(setTime);
          this.SendButton = true;
          _this.onUpButton_display = true;
          _this.onUpButton_txt = "";
          WitePrompt("onUpButton_txt" + _this.onUpButton_txt);
          setTime = null;
          _this.Time = 60;
        }
        else {
          _this.Time = _this.Time - 1;
          _this.onUpButton_txt = "(" + _this.Time + ")"
          WitePrompt("onUpButton_txt" + _this.onUpButton_txt);
          $("SendButton").removeAttr("disabled")

        }
      }, 1000)

    }


  }
