  dataHandle(meg: any) {
    //console.log(JSON.stringify(meg));

    if (meg['type'] == "getUser_Exist_ok") {
      //

      if (meg['User'] != "notExist") {
        // 
        let img: string = meg['UserImgUrl'];
        if (img.trim() != "") {
          this.UserImg = meg['UserImgUrl'];
        }

        WitePrompt(" " + meg['UserImgUrl']);
        this.appMin.setUserImg(meg['UserImgUrl']);
        this.isRL = true;
      }
      else {
        WitePrompt("")
        this.UserImg = "../../assets/img/.png";
        this.isRL = false;

      }

      this.isCenSubm = true; //
    }
    if (meg['type'] == "login_ret") {
      //
      if (meg["error"]) {
        //
        let error = meg["error"];
        if (error == "VerificationCode not send") {
          //
        }
        if (error == "login user is null") {
          //
        }
        if (error == "") {
          //
        }
        if (error == "l") {
          //
        }
        if (error == "") {
          //
        }



      } else {
        //   WitePrompt("")
        myLocalStorage.setItem('UserId', meg['userid'], 30);
        myLocalStorage.setItem("phonenumber", this.phonenumber, 30);
        myLocalStorage.setItem('UserImg', this.UserImg, 30);
        myLocalStorage.setItem('token', meg['token'], 30);
        // myLocalStorage.setItem("userimg",meg['userimg']);
        this.appMin.setUserId(meg['userid']);
        this.appMin.setLogin(true);
        let obj = {
          isAdd: true
        }
        //  this.navCtrl.setRoot(ContactPage, obj);
        this.ModalController.dismiss();
        
        var myEvent = new Event('addNotesUnReadCount');
        window.dispatchEvent(myEvent);// 
      
        this.router.navigate(['tabs/contact'], { queryParams: obj })

      }

    }
    if (meg['type'] == "register_ret") {
      //
      if (meg["error"]) {
        WitePrompt("1");

      }
      else if (meg["userid"]) {
        //

        myLocalStorage.setItem('UserId', meg['userid'], 30)
        myLocalStorage.setItem("phonenumber", this.phonenumber, 30)
        myLocalStorage.setItem('token', meg['token'], 30);
        this.appMin.setUserId(meg['userid']);
        this.appMin.setLogin(true);
        let obj = {
          isAdd: true
        }
        //  this.navCtrl.setRoot(ContactPage, obj);
        this.ModalController.dismiss();
        this.router.navigate(['/tabs/contact'], { queryParams: obj })
      }
      else {
        WitePrompt("2");
      }
    }
  }


